import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  { path: "/map", component: () => import("../components/YMap.vue") },
  {
    path: "/",
    name: "main-page",
    component: () => import("../views/landing/main-page.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
